// src/components/SeatGrid.tsx
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';

type Seat = {
  seatNumber: number;
  isReserved: boolean;
};

const MAX_SELECTION = 7;

export default function SeatGrid() {
  const TOTAL_SEATS = 80;
  const ROWS = 11;

  // Mocked: let's say seats 12, 13, 14, 50 are booked
  const bookedSeats = [12, 13, 14, 50];

  const [selectedSeats, setSelectedSeats] = useState<number[]>([]);

  const handleSelect = (seatNumber: number) => {
    if (bookedSeats.includes(seatNumber)) return;

    if (selectedSeats.includes(seatNumber)) {
      setSelectedSeats(selectedSeats.filter((n) => n !== seatNumber));
    } else {
      if (selectedSeats.length < MAX_SELECTION) {
        setSelectedSeats([...selectedSeats, seatNumber]);
      }
    }
  };

  const renderRows = () => {
    const seats: JSX.Element[] = [];
    let seatNumber = 1;

    for (let row = 1; row <= ROWS; row++) {
      const seatsInRow = row === 11 ? 3 : 7;
      const rowSeats = [];

      for (let i = 0; i < seatsInRow; i++) {
        const isReserved = bookedSeats.includes(seatNumber);
        const isSelected = selectedSeats.includes(seatNumber);

        rowSeats.push(
          <button
            key={seatNumber}
            disabled={isReserved}
            onClick={() => handleSelect(seatNumber)}
            className={cn(
              'w-10 h-10 rounded text-sm flex items-center justify-center border shadow',
              isReserved
                ? 'bg-gray-300 text-white cursor-not-allowed'
                : isSelected
                ? 'bg-green-500 text-white'
                : 'bg-white hover:bg-blue-100'
            )}
          >
            {seatNumber}
          </button>
        );

        seatNumber++;
      }

      seats.push(
        <div key={row} className="flex gap-2 mb-3 justify-center">
          {rowSeats}
        </div>
      );
    }

    return seats;
  };

  return (
    <div className="mt-6">
      <h2 className="text-xl font-semibold text-center mb-4">Select Your Seats</h2>
      {renderRows()}

      <div className="mt-6 text-center">
        <p className="text-sm text-muted-foreground">
          {selectedSeats.length} seat{selectedSeats.length !== 1 && 's'} selected
        </p>

        <Button
          disabled={selectedSeats.length === 0}
          className="mt-4"
          onClick={() => alert(`Book seats: ${selectedSeats.join(', ')}`)}
        >
          Confirm Booking
        </Button>
      </div>
    </div>
  );
}
